html {box-sizing: border-box;}
*, *:before, *:after {box-sizing: inherit;}
* {margin: 0;padding: 0;}
h1,h2,h3,h4,h5,h6 {font-weight: 400}
h1{font-size: 2.4rem; }
h2{font-size: 2.2rem; }
h3{font-size: 2.0rem; }
h4{font-size: 1.8rem; }
h5{font-size: 1.6rem; }
h6{font-size: 1.4rem; }
a {color: #5DA0E7; cursor: pointer;}
a:hover{color: #5DA0E7}
button{border: none; outline: none;background: none; cursor: pointer;}
/* html, body {width: 100%;height: 100%; position: relative;} */
html{
	height:100%;
	position: relative;
	display: flex;
}
body {
	height: 100%;
	width: 100%;
	position: relative; 
	display: flex; 
	font-family: 'Roboto', helvetica;
	font-weight: 400;
	font-size: 14px;
}
#root{
	flex: 1;
	display: flex;
	width: 100%;
}